.projects {
  display: flex;
  flex-direction: column;
  height: 544px;
  width: 100%;
  margin: auto;
  text-align: center;
}
.project-button {
  background-color: #facf0f;
  width: 183px;
  height: 44px;
  border-radius: 5px;
  border: none;
  margin-top: 20px;
  margin-left: auto;
  margin-right: 10px;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.project-buttonnew {
  background-color: #facf0f;
  width: 100px;
  height: 30px;
  border-radius: 5px;
  border: none;
  margin-top: 20px;
  margin-left: auto;
  margin-right: 10px;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.project-buttonnew:hover {
  background-color: black;
  color: #facf0f;
}
.project-button:hover {
  background-color: black;
  color: #facf0f;
}
.project--mainContent {
  display: flex;
  flex-direction: column;

  margin-top: -40px;
  text-align: left;
  margin-left: auto;
  margin-right: auto;

  width: 400px;
  height: 100%;
}
.content--one,
.content--two {
  width: 486px;
  height: 400px;
  /*border: 1px solid black;*/
  text-align: left;
  margin-left: 40px;
}

.projects p {
  font-size: 12px;
}
.aboutProject {
  width: 100%;
  height: 100%;

  /* border: 1px solid black;*/
}
.underline {
  width: 30px;
  height: 2px;
  background-color: #facf0f;
  margin-left: auto;
  margin-right: auto;
}
.container {
  /* border: 1px solid black;*/

  /*display: flex;*/
  /* justify-content: center;
  column-gap: 20px;*/

  width: 100%;
  margin-top: 30px;

  height: 230px;

  border-radius: 10px;
}
.projectImage {
  height: 400px;
  width: 309px;
  margin-top: 30px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
}
.imagedisplay {
  position: relative;
  width: 100%;
  height: 350px;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  text-align: center;
  border: 1px solid black;
  box-shadow: rgba(0, 0, 0, 0.9) 0px 5px 15px;
}
.imagedisplay:hover {
  border: 2px solid black;

  cursor: pointer;
}
.projectContainer {
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  width: 100%;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  column-gap: 50px;
  /*display: flex;
  flex-wrap: wrap;*/

  overflow-y: scroll;
  overflow-x: hidden;
  overflow-wrap: unset;
  scroll-behavior: smooth;
  flex-direction: row;
  border-radius: 10px;

  cursor: all-scroll;
  background-color: #fafafa;
  align-items: flex-start;
  border-radius: 5px;
}

.header-project {
  position: absolute;
}
.image {
  width: 100%;
  height: 50%;
  border-radius: 8px;
  filter: brightness(0.5) contrast(0.7);
}
.image:hover {
  filter: brightness(100%) contrast(0.9);
}
.heading {
  align-items: center;

  color: white;
  display: flex;

  font-family: sans-serif;
  justify-content: center;
  left: 20%;
  top: 15%;
  right: 20%;

  position: absolute;

  text-align: center;
}
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #facf0f;
}
input {
  border-style: none;
  border-bottom: 1px solid black;

  width: 200px;
  height: 30px;
}
.buttons {
  display: flex;
  column-gap: 30px;
}
.buttonClass {
  display: flex;
  justify-content: center;
  column-gap: 20px;
}
.searchButtons {
  width: 80px;
  height: 30px;

  background-color: #facf0f;
  border-radius: 2px;
  border: none;
  margin-top: 20px;

  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.searchButtons:hover {
  background-color: black;

  color: #facf0f;
}

@media only screen and (max-width: 768px) {
  .project-button {
    width: 60px;
    margin-right: 20px;
  }
  .about-button {
    width: 80px;
  }
  .container {
    width: 100%;
  }
  .searchButtons {
    width: 60px;
    height: 20px;
    font-weight: bold;
    font-size: 9px;
  }
}
