.about {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  margin: auto;
  text-align: center;
  margin-top: -20px;
}
.about-button {
  background-color: #facf0f;
  width: 183px;
  height: 44px;
  border-radius: 5px;
  border: none;
  margin: auto;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.about-button:hover {
  background-color: #bca754;
}

.about--mainContent {
  display: flex;
  justify-content: center;
  column-gap: 15px;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 100%;
  margin-top: 50px;
  margin-left: 10px;
  margin-right: auto;
  overflow-y: scroll;
  position: relative;

  scroll-behavior: smooth;

  /*border: 1px solid black;*/
}

.content--two {
  width: 450px;
  height: 200px;
  /* border: 1px solid black;*/
  text-align: left;
  background-color: #fafafa;
  border-radius: 10px;
  padding-left: 20px;
}
.content--one {
  text-align: left;
  width: 500px;
  height: 100%;
  background-color: #fafafa;
  border-radius: 10px;
  padding-left: 20px;
  padding-right: 20px;
}
.skillsContainer {
  display: flex;
  flex-wrap: wrap;
}

.skills {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  column-gap: 10px;
  row-gap: 10px;
}
.skills > button {
  margin: 5px;
  padding: 5px 20px 5px 20px;
  border-radius: 20px;
  border-color: transparent;
  box-shadow: 5px 5px 5px -2px;
}

.skills > div {
  padding: 20px 20px 5px 20px;
  border-radius: 5px;
  border: 0.5px solid black;
  justify-content: center;
  height: 50px;
  width: 40px;
  display: flex;
  flex-direction: column;
  column-gap: -10px;

  align-items: center;
  box-shadow: 3px 3px 3px -1px;

  transition: width 0.5s, height 0.5s;
}
.skills > div:hover {
  width: 52px;
  height: 56px;
  filter: brightness(0.7) contrast(0.7);
}
.skills > p {
  text-align: center;
  justify-content: center;
}
.tech > button {
  margin: 5px;
  color: white;
  padding: 5px 10px 5px 10px;
  border-radius: 20px;
  border-color: transparent;
  font-size: 10px;
  background-color: rgb(84, 84, 87);
}

.skills > button:hover {
  background-color: #facf0f;
  border-radius: 20px;
  border-color: transparent;
}

.about--heading {
  margin-top: 20px;
}

.about p {
  font-size: 12px;
}
.aboutMe {
  width: 100%;
  height: 200px;
}
.underline {
  width: 30px;
  background-color: #facf0f;
}
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #facf0f;
}
