.imageStyle {
  height: 40px;
  width: 40px;
  background-color: #facf0f;
  border-radius: 50%;
  margin-left: 40px;
  display: flex;
  text-align: center;
  justify-content: center;
}
img {
  height: 70%;
  width: 70%;
  margin: auto;
}
.social {
  display: flex;
  justify-content: space-around;
  align-items: center;

  align-items: center;
  width: 342px;
  height: 102px;
  text-align: left;
  margin-left: auto;
  margin-right: auto;
}
.content {
  display: flex;
  flex-direction: column;
  margin: auto;
  text-align: left;
  align-items: center;
}
.form {
  display: flex;
  flex-direction: column;
  height: 300px;
  width: 50%;
  gap: 20px;
  margin-left: 100px;
  margin-right: auto;
  margin-bottom: 150px;
  margin-top: 10px;
  padding-left: 70px;
  padding-right: 0px;

  padding-top: 30px;
  padding-bottom: 20px;
  border-bottom: 30px;
  border-radius: 10px;
  background-color: #ffffff;
  border-style: none;
}
.contact {
  display: flex;
  margin-left: 0%;
}
.form > input {
  height: 49px;
  width: 80%;
  border-radius: 5px;
  background-color: #fafafa;
  border-style: none;

  box-shadow: 5px 5px 5px lightgray;
}
.form > textarea {
  width: 80%;
  height: 91px;
  background-color: #fafafa;
  border-style: none;
  box-shadow: 5px 5px 5px lightgray;
}
.contact-button {
  background-color: #facf0f;
  width: 183px;
  height: 44px;
  border-radius: 5px;
  border: none;
  margin-top: 20px;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.contact-button:hover {
  background-color: black;
  color: #facf0f;
}
.content > a {
  font-size: small;
}
.modal {
  margin: auto;
  color: rgb(159, 23, 23);
  font-size: 7px;
  text-align: left;
}
@media only screen and (max-width: 768px) {
  .social {
    display: block;
    width: 100px;
    height: 90px;
  }
  .social > div > p {
    font-size: x-small;
  }
  .social > div > a {
    font-size: x-small;
  }
  .contact-button {
    width: 100px;
  }
  .form {
    padding-left: 20px;
    width: 100%;
    margin-left: 20px;
  }
  .imageStyle {
    height: 30px;
    width: 30px;
  }
}
